* {
  touch-action: none;
}

html {
  height: 100%;
  overflow: scroll;
}

body {
  margin: 0 auto;
  font-family: "Inter", sans-serif;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__wrapper {
  margin: 0 auto;
  min-width: 320px;
}
@media (min-width: 600px) {
  .page__wrapper {
    width: 600px;
  }
}

.list {
  list-style: none;
  font-size: 13px;
  line-height: 16px;
  padding-left: 30px;
}
.list--links {
  margin-top: 300px;
  margin-bottom: 0;
}
.list--social {
  margin-top: 50px;
  margin-bottom: 100px;
}
@media (min-width: 375px) {
  .list {
    font-size: 15px;
    line-height: 18px;
    padding-left: 50px;
  }
}
@media (min-width: 600px) {
  .list {
    margin-left: 102px;
  }
  .list--links {
    margin-bottom: 0;
  }
  .list--social {
    margin-top: 60px;
    padding-bottom: 40px;
  }
}

.list a {
  text-decoration: none;
  color: #000000;
}
.list a:hover {
  opacity: 0.5;
}

.audio__icon {
  border: none;
  background-color: transparent;
  position: fixed;
  top: 15px;
  right: 16px;
}

.audio__on,
.audio__off {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.picture img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 600px) {
  .picture img {
    width: 320px;
    height: 463px;
    margin: 0 auto;
  }
}

.contact {
  text-decoration: none;
  color: #000000;
  font-size: 15px;
  background-color: rgba(195, 192, 168, 0.948);
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 6px 8px;
}
.contact:hover {
  background-color: rgba(185, 182, 159, 0.948);
}
.contact:active {
  background-color: rgba(169, 166, 144, 0.948);
}